import React from 'react';
import { Link } from  'react-router-dom';
import './Navigation.scss';

const Navigation = () => {
    return (
        <div className="navi">
            <Link className="lnk" to="/">
            HOME
            </Link>
            <Link className="lnk" to="/about">
            ABOUT
            </Link>
        </div>
    );
};

export default Navigation;